import React, { useState, useRef } from "react";
import style from "styled-components";

const SearchSection = style.section`
  position: absolute;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #666666;
  font-weight: bold;
  z-index: 99;
  `;

const SearchContainer = style.div`
  background-color: white;
  width: 86%;
  padding: 20px 15px 14px;
`;

const SearchHeader = style.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
`;

const SearchBox = style.div`
  display: flex;
  cursor: pointer;
  box-sizing: border-box;
`;

const SearchList = style.div`
  width: 100%;
  cursor: pointer;
  padding-right: 21px;
  max-height: 540px;
  overflow: auto;
`;
// 

const SearchCategory = style.div`
  text-align: center;
  cursor: pointer;
  padding: 10px 0;
  `;
// 

const SearchListDetail = style.div`
  width: 100%;
  overflow: auto;
  text-align: center;
  cursor: pointer;
  max-height: 540px;
  overflow: auto;
`;

const DetailContent = style.div`
  cursor: pointer;
  padding: 10px 0;
`;

const SearchBtn = style.div`
  padding: 14px 0;
  background: #56AEDF;
  border-radius: 5px;
  color: white;
  text-align: center;
  margin-top: 14px;
`;

const SearchDes = style.div`
  border-bottom: 2px solid #56aedf;
  padding: 10px 0;
  text-align: center;
`;

