import React from "react";
import styled from "styled-components";
import InfoBar from "./InfoBar";
const OwnerContainer = styled.div`
  color: #666666;
  font-weight: 700;
  max-width: 900px;
  margin: 52px auto 70px;
`;
const OwnerHead = styled.div`
  padding: 24px 12px 0;
`;
const OwnerHeadDes = styled.div`
  padding-bottom: 10px;
  font-size: 14px;
  color: #888888;
`;
const OwnerHeadTitle = styled.div`
  padding-bottom: 10px;
`;

const OwnerInfoBox = styled.div`
  padding: 24px 0;
  border-bottom: 1px solid #d9d9d9;
`;

const ownerex = [
  { title: "1.    ?", per: 43 },
  { title: "2.    ?", per: 80 },
  { title: "3.     ?", per: 7 },
  { title: "4.   ?", per: 100 },
  { title: "5.  ?", per: 0 },
];
function ProductOwner() {
  return (
    <OwnerContainer>
      <OwnerHead>
        <OwnerHeadTitle> </OwnerHeadTitle>
        <OwnerHeadDes>(   .)</OwnerHeadDes>
      </OwnerHead>
      {ownerex.map((li, Index) => (
        <OwnerInfoBox key={Index}>
          <InfoBar title={li.title} percentage={li.per} />
        </OwnerInfoBox>
      ))}
    </OwnerContainer>
  );
}

export default ProductOwner;
