import React from "react";
import styled from "styled-components";
import InfoBar from "../product/InfoBar";
import { Link } from "react-router-dom";

const OwnerContainer = styled.div`
  color: #666666;
  font-weight: 700;
  max-width: 900px;
  margin: 52px auto 0;
`;

const OwnerHead = styled.div`
  padding: 24px 12px 0;
`;

const OwnerHeadDes = styled.div`
  padding-bottom: 10px;
  font-size: 14px;
  color: #888888;
`;

const OwnerName = styled.div`
  padding: 0 8px 2px 0;
  border-bottom: 2px solid #888888;
`;

const OwnerNameText = styled.span`
  color: #56aedf;
  margin-right: 20px;
`;

const OwnerInfoBox = styled.div`
  padding: 24px 0;
  border-bottom: 1px solid #d9d9d9;
`;

const NickNameLink = styled(Link)`
  display: flex;
  padding-bottom: 12px;
  align-items: center;
  color: #666666;
`;

const ownerex = [
  { title: "1.    ?", per: 43 },
  { title: "2.    ?", per: 80 },
  { title: "3.     ?", per: 7 },
  { title: "4.   ?", per: 100 },
  { title: "5.  ?", per: 20 },
];

function Profile() {
  return (
    <OwnerContainer>
      <OwnerHead>
        <NickNameLink to={"/mypage/profileEdit"}>
          <>
            <OwnerName>
              <OwnerNameText></OwnerNameText>
              <span></span>
            </OwnerName>
            <img src={require("../img/side.png")} />
          </>
        </NickNameLink>
        <OwnerHeadDes>(   .)</OwnerHeadDes>
      </OwnerHead>
      {ownerex.map((li, Index) => (
        <OwnerInfoBox key={Index}>
          <InfoBar title={li.title} percentage={li.per} />
        </OwnerInfoBox>
      ))}
    </OwnerContainer>
  );
}

export default Profile;
